
.fl-if_FormField {
  transform: scale(.98);
  margin: 1em 0;
  transition: all .3s ease-in-out;

  //Inactive styles
  opacity: .2;
  pointer-events: none;

  &:first-child {
    margin-top: 10vh;
  }

  &:last-child {
    margin-bottom: 10vh;
  }

  &--active {
    pointer-events: all;
    opacity: 1;
    // margin: 5em auto;
    transform: scale(1);
  }
}

.fl-if_FormField-legend {
  font-size: $s-font-medium-large;
  padding-left: $s-left-padding;
  padding-right: $s-right-padding;
  margin: 0;
  text-align: left;
}
